I.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  green {
    color: white;  
    background-color: green;
  }
  
  button {
    font-size: 18px;
    padding: 10px;
    border-radius: 12px;
    background-color: green;
    color: white;
    font-weight: 70;
  }
  
  input {
    font-size: 16px;
    padding: 10px;
    border-radius: 12px;
    background-color: lightgrey;
    color: blue;
    font-weight: 70;
  }
  
  .card {
    border: 0.5px dotted #ddd;
    width: 1100px;
    margin: 1px;
    font-family: Montserrat, sans-serif;
    text-align: left; 
    font-size: 15px;
  }
  
  .card1 {
    ol {
      list-style: none;
      /*  命名自訂標號變數  */
      counter-reset: my-counter;
      font-size: 15px;
    }
    ol li {
    /*  使用自訂標號  */
      counter-increment: my-counter;
    /* 段落首行縮排 */
      text-indent: -1em;
      font-size: 15px;
    }
    /* 以偽元素自訂標號樣式 */
    ol li::before {
      content: "("counter(my-counter) ")";
      color: blue;
      font-weight: bold;
      font-size: 15px;
    }
  }
  
  
  